<template >
  <a-page-header
    style="border: 1px solid rgb(235, 237, 240)"
    title="出行助手"
    :breadcrumb="{ props: { routes } }"
  >
    <template slot="extra">
      <a-auto-complete
        placeholder="请输入地名"
        :data-source="datas"
        v-model="value"
        allow-clear
      ></a-auto-complete>
    </template>
  </a-page-header>
</template>

<script>
import Locations from "../../data/outting/locating.js";
export default {
  props:["routes"],
  components: {},
  model: { prop: "area", event: "change" },
  data() {
    return {
      value: this.area,
      datas: Object.keys(Locations)    };
  },
  watch: {
    value(v) {
      this.$emit("change", v || "");
    },
  },
};
</script>
